/**
 * connect() reactreduxstore
 * @param mapStateToProps Redux store
 * @param mapDispatchToProps  mapDispatchToProps dispatch  props 
 * */
const VisibleTodoList = connect(
  mapStateToProps,
  null
)(TodoList)

export default VisibleTodoList


