import React, {Component} from 'react'
import {connect} from "react-redux";
import {copyTodo, deleteTodo, toggleTodo} from "../../../store/actions";

class Todo extends Component {

  // (initialization)
  constructor() {
    super();

    this.state = {date: new Date()};
  }

  // (Mounting)
  // componentWillMountrendercomponentDidMount

  componentWillMount() {
    console.log('Todo componentWillMount ', this.props);
  }

  render() {
    console.log('Todo render');
    let {completed, text, index, deleteTodo, copyTodo, toggleTodo} = this.props;
    return (
      <li className="list-group-item">
      <span style={{
        textDecoration: completed ? 'line-through' : 'none'
      }}>{text}</span>
        <div style={{float: 'right'}}>
          <span onClick={() => toggleTodo(index)}></span>
          <span onClick={() => deleteTodo(index)}></span>
          <span onClick={() => copyTodo(index)}></span>
        </div>
      </li>
    )
  }

  componentDidMount() {
    console.log('Todo componentDidMount ', this.props);
  }

  // (update)
  // componentWillReceiveProps>shouldComponentUpdate --> componentWillupdate --> componentDidUpdate

  // componentWillReceiveProps this.setState() 
  componentWillReceiveProps(nextProps) {
    console.log('Todo componentWillReceiveProps props');
  }

  // shouldComponentUpdate:falserender
  // shouldComponentUpdate(nextProps) {
  //   // if (nextProps.someThings === this.props.someThings) {
  //   //   return false
  //   // }
  //   return true;
  // }

  // componentWillUpdate:
  componentWillUpdate(nextProps) {
    console.log('Todo componentWillUpdate ');
  }

  // componentDidUpdate:
  componentDidUpdate(nextProps) {
    console.log('Todo componentDidUpdate ', JSON.stringify(nextProps));
  }

  // 
  componentWillUnmount() {
    console.log('Todo componentWillUnmount ', JSON.stringify(this.props));
  }
}

const mapDispatchToProps = {
  deleteTodo,
  copyTodo,
  toggleTodo
}

Todo = connect(
  null,
  mapDispatchToProps
)(Todo)

export default Todo


