import {CHANGE_TAB} from "../actions";

let homeData = {
  curSubTab: "CX",
  curShowList: []
}

//  reducer Array.prototype.reduce(reducer, ?initialValue)  reducer  reducer 
//
// 
//  API 
//  Date.now()  Math.random()

/**
 *  reducer (state, action) => state 
 *  action  state  state
 *
 * state 
 *  Immutable.js 
 *  state 
 *
 *  `switch` (helper)
 * 
 */
export function home(state = homeData, action) {
  switch (action.type) {
    case CHANGE_TAB:
      // return {
      //   ...state,
      //   ...{curSubTab: action.data}
      // }

      return Object.assign({}, state, {
        curSubTab: action.data
      })
    default:
      return state
  }
}


