import {combineReducers, createStore, applyMiddleware} from 'redux';
import thunkMiddleware from 'redux-thunk';
import {createLogger} from 'redux-logger';
import * as reducers from './reducers';
import {CHANGE_INFO, CHANGE_TAB, decrement, increment} from "./actions";
import {fetchPosts} from "./actions/globalActionCreators";

const loggerMiddleware = createLogger()

// combineReducers()  reducer reducer  key  state  reducer 
// combineReducers  reducer  export  reducer  import * as reducers  key  object
let reducer = combineReducers(reducers)

// Store (action,reducers)Store 

//  state
//  getState()  state
//  dispatch(action)  state
//  subscribe(listener) ;
//  subscribe(listener) 
export let store = createStore(
  reducer,
  applyMiddleware(
    thunkMiddleware, // dispatch()
    loggerMiddleware // middlewareaction
  )
);

//  state 
//  subscribe() 
// eslint-disable-next-line
const unsubscribe = store.subscribe(() => {
  // console.log("store.state:", store.getState())
})

//  state  dispatch  action
// action 
store.dispatch(increment(99));
store.dispatch(increment(199));
store.dispatch(decrement(88));
store.dispatch({
  type: CHANGE_TAB,
  data: "OYNN"
});
store.dispatch({
  type: CHANGE_INFO,
  data: {
    name: "",
    sex: "W",
    age: 20,
    birthday: "2000.6.15"
  }
});
store.dispatch(fetchPosts('reactjs')).then(res => {
  // console.log('res:', res);
  // console.log(store.getState());
})

//  state 
// unsubscribe()


