import './User.css';
import React, {Component} from 'react';
import {withRouter} from 'react-router-dom';

class UserEdit extends Component {

  constructor(props) {
    super(props);

    this.state = {};
  }

  onClickSave() {
    console.log("...");
    setTimeout(() => {
      console.log("/user");
      this.props.history.push({pathname: '/user'})
    }, 1000)
  }

  render() {
    return (
      <div className="user-edit">
        <h2 style={{borderBottom: "1px solid #e4e4e4"}}></h2>
        <br/>
        <p style={{color: 'red', textAlign: 'center'}}></p>
        <br/>
        <button className='btn btn-primary' onClick={this.onClickSave.bind(this)}></button>
      </div>
    )
  }
}

export default withRouter(UserEdit)


