import './Header.css'
import React, {Component} from 'react'
import Pobsub from 'pubsub-js';

export default class Header extends Component {
  static defaultProps = {
    mainTitle: "",
    subTitle: ""
  }

  constructor() {
    super();

    this.state = {
      curSubTab: "CX"
    };
  }

  onClickSubNav(data, evt) {
    if (this.state.curSubTab !== data) {
      // console.log('subtab:', data, evt)
      Pobsub.publish(`CLICK_${data}`)
      this.setState({curSubTab: data})
    }
  }

  render() {
    return (
      <div className={'home-header'}>
        <h2>{this.props.mainTitle}<span>&nbsp;&nbsp;{this.props.subTitle}</span></h2>
        <div className={'scroller'}>
          <button onClick={this.onClickSubNav.bind(this, 'CX')}></button>
          <button onClick={this.onClickSubNav.bind(this, 'OYNN')}></button>
          <button onClick={() => this.props.funa(',')}></button>
          <button onClick={() => this.props.funa(',Lisa')}>Lisa</button>
          <button onClick={() => Pobsub.publish("CLICK_VIEW", ',')}></button>
          <button onClick={() => Pobsub.publish("CLICK_NAVIGATION", ',')}></button>
          <button onClick={() => this.props.funa('')}>Selina</button>
          <button onClick={() => this.props.funa('')}>Hebe</button>
          <button onClick={() => this.props.funa('')}>Ella</button>
        </div>
      </div>
    )
  }
}


