export const FETCH_POSTS_REQUEST = 'fetch_posts_request';
export const FETCH_POSTS_FAILURE = 'fetch_posts_failure';
export const FETCH_POSTS_SUCCESS = 'fetch_posts_success';

export const REQUEST_POSTS = 'request_posts';
export const RECEIVE_POSTS = 'receive_posts';
export const ERROR_POSTS = 'error_posts';
export const INVALIDATE_SUBREDDIT = 'invalidate_subreddit';

export function requestPosts(data) {
  return {
    type: REQUEST_POSTS,
    data
  }
}

export function receivePosts(data) {
  return {
    type: RECEIVE_POSTS,
    data,
    receivedAt: Date.now()
  }
}

export function errorPosts(data) {
  return {
    type: ERROR_POSTS,
    data
  }
}

export function invalidateSubreddit(data) {
  return {
    type: INVALIDATE_SUBREDDIT,
    data
  }
}

//  thunk action 
//  action  
// store.dispatch(fetchPosts('reactjs'))
export function fetchPosts(subreddit) {

  // Thunk middleware 
  //  dispatch 
  //  dispatch action

  return function (dispatch) {

    //  dispatch state 
    // API 

    dispatch(requestPosts(true))

    // thunk middleware 
    //  dispatch 

    //  promise
    //  redux middleware 

    return new Promise((resolve, reject) => {
      setTimeout(() => {
        let res = {
          code: 200,
          data: '',
          message: "ok"
        }
        resolve(res);
      }, 1000);
    }).then(res => {
      //  catch
      //  dispatch 
      //  'Unexpected batch number' 
      // https://github.com/facebook/react/issues/6895
      dispatch(receivePosts(false))
      dispatch(invalidateSubreddit(res))

      return res;
    }, error => {
      dispatch(errorPosts(error));
    })
  }
}


